.Button {
	background-color: transparent;
	border: none;
	color: white;
	outline: none;
	cursor: pointer;
	font: inherit;
	padding: 10px;
	margin: 10px;
	font-weight: bold;
}
@media (max-width: 330px) {
	.Button {
		padding: 5px;
	}
}
.Button:first-of-type {
	margin-left: 0;
}
.Success {
	background-color: transparent;
	border: 3px solid rgb(255, 218, 116);
	color: rgb(255, 218, 116);
	border-radius: 30px;
}
.Success:hover {
	background-color: rgb(255, 218, 116);
	color: #3d2222;
}

.Danger {
	background-color: transparent;
	border: 3px solid #944317;
	border-radius: 30px;
	color: #944317;
}
.Danger:hover {
	background-color: #944317;
	color: #3d2222;
}
.Button:disabled,
.Button[disabled] {
	color: rgb(173, 158, 148);
	border: 3px solid rgb(173, 158, 148);
	cursor: unset;
}
.Button:disabled:hover,
.Button[disabled]:hover {
	background-color: transparent;
}
