.NavigationItems {
	margin: 0;
	padding: 0;
	list-style: none;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
}

@media (min-width: 530px) {
	.NavigationItems {
		margin: 0;
		padding: 0;
		list-style: none;
		display: flex;
		flex-direction: row;
		align-items: center;
		height: 100%;
	}
}
